<script setup>
  import {
    Finished
  } from '@element-plus/icons-vue'
  import {ref, toRefs, defineProps, defineEmits} from "vue"
  import FormCheck from "../../../utils/formCheck";
  import ClusterLabel from "./label"
  import {useVmodel} from "../../../hooks/useVmodel";
  import clusterApi from "../../../api/cluster";
  import {ElMessage, ElMessageBox} from 'element-plus';

  const emit = defineEmits();
  const props = defineProps({
    modelValue: Object
  });
  const modelValue = useVmodel(props);

  const RefForm = ref(null)

  const formRules = [{
    clusterName: FormCheck.required("请输入集群名称")
  }]

  function changeStep(idx) {
    emit('changeStep', idx)
  }
  function createEvent() {
    emit('createEvent')
  }

  // 系统盘类型
  const osDiskTypeList = ref([])

  function getOsDiskTypeList() {
    clusterApi.getOsDiskTypeList().then(res => {
      if (res.result == true) {
        osDiskTypeList.value = res.data
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getOsDiskTypeList()

  function diskTypeToName(type) {
    let item = osDiskTypeList.value.find(itm => {
      return itm.dictValue == type
    }) || {}

    return item.dictName
  }

  function getLabelsStr(tagMap) {
    let str = ''
    for (let key in tagMap) {
      if (str) {
        str += '、' + key + '：' + tagMap[key]
      } else {
        str += key + '：' + tagMap[key]
      }
    }
    return str
  }
</script>

