<template>
  <div class="label">
    <div class="label-row" v-for="(labelItem, idx) in labels">
      <div class="label-input">
        <el-select style="width: 100%;" placeholder="请选择标签key" v-model="labelItem.key" @change="changeKey(labelItem)" filterable allow-create default-first-option>
          <el-option
                  v-for="option in options"
                  :key="option.dictValue"
                  :label="option.dictName"
                  :value="option.dictValue"
                  :disabled="option.disabled"
          />
        </el-select>
      </div>
      <div class="label-input">
        <el-select style="width: 100%;" placeholder="请选择/输入标签值" v-model="labelItem.value"
                   @change="changeValue(labelItem)" filterable allow-create default-first-option>
          <el-option
                  v-for="option in valueOptions[labelItem.key]"
                  :key="option.tagVal"
                  :label="option.tagVal"
                  :value="option.tagVal"
          />
        </el-select>
        <!--<el-input v-model="labelItem.value" placeholder="请输入值" maxlength="50" clearable @change="changeValue(labelItem)"></el-input>-->
      </div>
      <div class="label-btn">
        <el-button text :icon="Delete" circle @click="removeRow(idx)"/>
      </div>
    </div>
    <div style="width: 100%;padding-right: 50px;">
      <div class="add-btn" @click="addRow" v-if="options.length > labels.length">添加标签</div>
    </div>
  </div>
</template>

<script setup>
  import {
    Delete
  } from '@element-plus/icons-vue'
  import {ref, toRefs, defineProps, defineEmits, computed} from "vue"
  import {useVmodel} from "@/hooks/useVmodel";
  import clusterApi from "../../../api/cluster";
  import {ElMessage, ElMessageBox} from 'element-plus';

  const emit = defineEmits();
  const props = defineProps({
    modelValue: Object
  });
  const modelValue = useVmodel(props);

  const options = ref([])

  const labels = ref([])

  function initLabels() {
    labels.value = []
    for (let key in modelValue.value) {
      if (key) {
        labels.value.push({
          key: key,
          value: modelValue.value[key]
        })
      }
    }
    if (labels.value.length == 0) {
      labels.value.push({
        key: '',
        value: ''
      })
    }

    resetOptions()
  }

  function setModelValue() {
    for (let key in modelValue.value) {
      delete modelValue.value[key]
    }

    labels.value.forEach(item => {
      if (item.key) {
        modelValue.value[item.key] = item.value || ''
      }
    })
  }

  function resetOptions() {
    options.value.forEach(opt => {
      let dictValue = opt.dictValue
      if (modelValue.value[dictValue] != undefined) {
        opt.disabled = true
      } else {
        opt.disabled = false
      }
    })
  }

  function changeValue(labelItem) {
    setModelValue()
  }

  function changeKey(labelItem) {
    labelItem.value = ''
    setModelValue()

    resetOptions()

    getKeyValues(labelItem.key)
  }

  const valueOptions = ref({})
  
  function getKeyValues(key) {
    if (valueOptions.value[key] && valueOptions.value[key].length) {
      return ;
    }
    clusterApi.getTagValueList({tagKey: key}).then(res => {
      if (res.result == 1) {
        valueOptions.value[key] = res.data
      } else {
        ElMessage.error(res.message)
      }
    })
  }

  function addRow() {
    labels.value.push({key: '', value: ''})
  }

  function removeRow(idx) {
    let item = labels.value[idx]
    let key = item.key
    if (key) {
      delete modelValue.value[key]

      resetOptions()
    }
    labels.value.splice(idx, 1)
  }

  function gettagKeyList() {
    clusterApi.gettagKeyList().then(res => {
      if (res.result == true) {
        options.value = res.data

        initLabels()
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  gettagKeyList()

</script>

