<template>
  <div class="step-3" v-loading="stepLoading" element-loading-text="校验数据库有效性中....">
    <el-form size="large" ref="RefForm" :model="modelValue" :rules="formRules" :scroll-to-error="true" label-width="120px">
      <div class="full-page-block-div">
        <div class="block-title">网络配置</div>
        <el-form-item label="选择网络" prop="vNet">
          <el-select class="input-width" placeorder="请选择网络" v-model="modelValue.vNet" @change="vNetChange">
            <el-option
                    v-for="option in netList"
                    :key="option.dictValue"
                    :label="option.dictName"
                    :value="option.dictValue"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="选择子网" prop="subNet">
          <el-select class="input-width" placeorder="请选择子网" v-model="modelValue.subNet" @change="subNetChange">
            <el-option
                    v-for="option in subNetList"
                    :key="option.resourceId"
                    :label="option.name"
                    :value="option.resourceId"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="主安全组" prop="masterSecurityGroup">
          <el-select class="input-width" placeorder="请选择主安全组" v-model="modelValue.masterSecurityGroup" @change="masterSecurityGroupChange">
            <el-option
                    v-for="option in masterSecurityGroupList"
                    :key="option.dictValue"
                    :label="option.dictName"
                    :value="option.dictValue"
            />
          </el-select>
          <div class="from-item-tip">master节点安全组</div>
        </el-form-item>
        <el-form-item label="子安全组" prop="slaveSecurityGroup">
          <el-select class="input-width" placeorder="请选择子安全组" v-model="modelValue.slaveSecurityGroup" @change="slaveSecurityGroupChange">
            <el-option
                    v-for="option in subSecurityGroupList"
                    :key="option.dictValue"
                    :label="option.dictName"
                    :value="option.dictValue"
            />
          </el-select>
          <div class="from-item-tip">core\task节点安全组</div>
        </el-form-item>
      </div>
      <div class="full-page-block-div">
        <div class="block-title">节点配置</div>
        <el-form-item label="登录方式" prop="keypairId">
          <el-select class="input-width" placeorder="请选择登录方式" v-model="modelValue.keypairId">
            <el-option
                    v-for="option in keypairList"
                    :key="option.dictValue"
                    :label="option.dictName"
                    :value="option.dictValue"
            />
          </el-select>
          <div class="from-item-tip">选择密钥对</div>
        </el-form-item>
        <el-form-item label="高可用">
          <el-switch
                  v-model="modelValue.isHa"
                  inline-prompt
                  active-text="开启"
                  :active-value="1"
                  :inactive-value="0"
                  inactive-text="关闭"
                  @change="isHaChange"
          />
        </el-form-item>
        <el-form-item label="系统盘">
          <el-select class="input-width-2" placeorder="请选择磁盘类型" v-model="modelValue.osDiskType" @change="osDiskChange">
            <el-option
                    v-for="option in osDiskTypeList"
                    :key="option.dictValue"
                    :label="option.dictName"
                    :value="option.dictValue"
            />
          </el-select>
          <el-input class="input-width-2" v-model="modelValue.diskSize" @change="osDiskChange" :disabled="true">
            <template #append>GB</template>
          </el-input>
        </el-form-item>
        <el-form-item label="节点设置">
          <el-table v-if="refresh" :data="modelValue.instanceGroupSkuCfgs" header-row-class-name="theader" border style="width: 100%" :span-method="objectSpanMethod">
            <el-table-column prop="runTiming" label="节点类型" min-width="200">
              <template #default="scope">
                {{scope.row.vmRole}}节点配置
              </template>
            </el-table-column>
            <el-table-column prop="scriptName" label="节点规格" min-width="300">
              <template #default="scope">
                <div>
                  <div v-if="scope.row.vmRole == 'Master' && !modelValue.isHa">将部署在Ambari节点</div>
                  <div style="display: flex;align-items: center;" v-else>
                    <div style="flex: 1;">
                      <div>{{ scope.row.skuName }} {{scope.row.vCPUs}}核{{scope.row.memoryGB}}G</div>
                      <div>系统盘：{{diskTypeToName(scope.row.osVolumeType)}}云盘{{scope.row.osVolumeSize}}G*1</div>
                      <div>数据盘：{{diskTypeToName(scope.row.dataVolumeType)}}云盘{{scope.row.dataVolumeSize}}G*1</div>
                    </div>
                    <div>
                      <el-button type="primary" text size="small" @click="editDisk(scope.row)" :disabled="modelValue.isHa != 1 && scope.row.vmRole == 'Master'">编辑</el-button>
                    </div>
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="scriptPath" label="节点数量" min-width="220">
              <template #default="scope">
                <div>
                  <div v-if="scope.row.vmRole == 'Master' && !modelValue.isHa">将部署在Ambari节点</div>
                  <el-input-number v-model="scope.row.cnt" :min="scope.row.minNum" :max="999" :disabled="scope.row.vmRole == 'Master' || scope.row.vmRole == 'Ambari'" :step-strictly="true" v-else/>
                  <!--(modelValue.isHa == 1 && scope.row.vmRole == 'Ambari')-->
                </div>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
      </div>
      <div class="full-page-block-div">
        <div class="block-title">Ambari元数据</div>
        <div class="flex-row">
          <el-form-item label="主机" label-width="80px" prop="ambariDbCfgs.url" style="width: 50%;">
            <el-input v-model="modelValue.ambariDbCfgs.url" placeholder="请输入主机" show-word-limit clearable/>
          </el-form-item>
          <el-form-item label="端口" label-width="80px" prop="ambariDbCfgs.port" style="width: 20%;">
            <el-input v-model="modelValue.ambariDbCfgs.port" placeholder="请输入数据库端口" show-word-limit clearable/>
          </el-form-item>
          <el-form-item label="库名" label-width="80px" prop="ambariDbCfgs.database" style="width: 30%;">
            <el-input v-model="modelValue.ambariDbCfgs.database" placeholder="请输入数据库库名" show-word-limit clearable/>
          </el-form-item>
        </div>
        <div class="flex-row">
          <el-form-item label="用户名" label-width="80px" prop="ambariDbCfgs.account" style="width: 50%;">
            <el-input v-model="modelValue.ambariDbCfgs.account" placeholder="请输入用户名" show-word-limit maxlength="50" clearable/>
          </el-form-item>
          <el-form-item label="密码" label-width="80px" prop="ambariDbCfgs.password" style="width: 50%;">
            <el-input v-model="modelValue.ambariDbCfgs.password" placeholder="请输入密码" type="password" show-password show-word-limit maxlength="50" clearable/>
          </el-form-item>
        </div>
      </div>
      <div class="full-page-block-div" v-if="hasHive">
        <div class="block-title">Hive元数据</div>
        <div class="flex-row">
          <el-form-item label="主机" label-width="80px" prop="hiveMetadataDbCfgs.url" style="width: 50%;">
            <el-input v-model="modelValue.hiveMetadataDbCfgs.url" placeholder="请输入数据库连接" show-word-limit clearable/>
          </el-form-item>
          <el-form-item label="端口" label-width="80px" prop="hiveMetadataDbCfgs.port" style="width: 20%;">
            <el-input v-model="modelValue.hiveMetadataDbCfgs.port" placeholder="请输入数据库端口" show-word-limit clearable/>
          </el-form-item>
          <el-form-item label="库名" label-width="80px" prop="hiveMetadataDbCfgs.database" style="width: 30%;">
            <el-input v-model="modelValue.hiveMetadataDbCfgs.database" placeholder="请输入数据库库名" show-word-limit clearable/>
          </el-form-item>
        </div>
        <div class="flex-row">
          <el-form-item label="用户名" label-width="80px" prop="hiveMetadataDbCfgs.account" style="width: 50%;">
            <el-input v-model="modelValue.hiveMetadataDbCfgs.account" placeholder="请输入用户名" show-word-limit maxlength="50" clearable/>
          </el-form-item>
          <el-form-item label="密码" label-width="80px" prop="hiveMetadataDbCfgs.password" style="width: 50%;">
            <el-input v-model="modelValue.hiveMetadataDbCfgs.password" placeholder="请输入密码" type="password" show-password show-word-limit maxlength="50" clearable/>
          </el-form-item>
        </div>
      </div>
      <div class="full-page-block-div">
        <div class="block-title">日志存储</div>
        <el-form-item label="日志桶">
          <el-input style="width: 50%;" v-model="modelValue.logPath" placeholder="请输入日志桶" show-word-limit maxlength="100" clearable/>
        </el-form-item>
      </div>
    </el-form>
    <div class="full-page-buttons">
      <el-button class="pre-step" size="large" @click="preEvent">上一步</el-button>
      <el-button type="primary" size="large" class="next-step" @click="nextEvent"  :loading="stepLoading">下一步</el-button>
    </div>
    <el-dialog
            class="center-dialog"
            v-model="dialogVisible"
            title="节点规格"
            width="800"
            destroy-on-close
    >
      <div class="dialog-content">
        <div class="dialog-tip">Master节点建议选择内存较大的实例规格，推荐内容大小至少8G。磁盘建议选择云盘可以让集群获得更高的稳定性。</div>
        <el-form ref="Ref_editForm" :model="editForm" :rules="scriptFormRules" label-width="120px" style="width: 90%;">
          <el-form-item label="规格选择" prop="vmskuName">
            <el-select style="width: 100%;" placeorder="请选择规格" v-model="editForm.vmskuName">
              <el-option
                      v-for="option in vmskuList"
                      :key="option.name"
                      :label="option.name"
                      :value="option.name"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="数据盘" prop="disk">
            <div style="display: flex;width: 100%;">
              <el-select style="flex: 1;" placeorder="请选择磁盘类型" v-model="editForm.dataVolumeType">
                <el-option
                        v-for="option in osDiskTypeList"
                        :key="option.dictValue"
                        :label="option.dictName"
                        :value="option.dictValue"
                />
              </el-select>
              <el-input  style="flex: 1;margin-left: 15px;" v-model="editForm.dataVolumeSize">
                <template #append>GB</template>
              </el-input>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="saveEvent">保存</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
  import {
    Check
  } from '@element-plus/icons-vue'
  import {ref, reactive, toRefs, defineProps, defineEmits, nextTick, computed} from "vue"
  import FormCheck from "../../../utils/formCheck";
  import {useVmodel} from "../../../hooks/useVmodel";
  import clusterApi from "../../../api/cluster";
  import {ElMessage, ElMessageBox} from 'element-plus';

  const emit = defineEmits();
  const props = defineProps({
    modelValue: Object
  });
  const modelValue = useVmodel(props);

  const stepLoading = ref(false)

  const RefForm = ref(null)

  const isNextClick = ref(false)

  function checkConnect(d, type, callback) {
    if (d.url && d.account && d.password && d.port && d.database) {
      clusterApi.checkConnect({
        url: d.url,
        account: d.account,
        password: d.password,
        port: d.port,
        database: d.database,
        type: type
      }).then(res => {
        if (res.result == true) {
          callback(true);
        } else {
          // ElMessage.error(res.errorMsg)
          callback(false, res.errorMsg);
        }
      }).catch(err => {
        callback(false, '检查失败！');
      })
    } else {
      callback(true);
    }
  }

  let timer1 = null, result1 = false, err1 = null, timer2 = null, result2 = false, err2 = null

  function resultFunc(result, callback, err) {
    if (result == true) {
      callback()
    } else {
      callback(err)
    }
  }
  
  function ambariDbCfgsCheck(type) {
    return {
      validator: (rule, value, callback) => {
        if (type == 0) {
          if (timer1) {
            resultFunc(result1, callback, err1)
            return ;
          }
          checkConnect(modelValue.value.ambariDbCfgs, type, (result, err) => {
            result1 = result
            err1 = err
            resultFunc(result1, callback, err1)
            timer1 = setTimeout(() => {
              timer1 = null
            }, 200)
          })
        } else {
          if (timer2) {
            resultFunc(result2, callback, err2)
            return ;
          }
          checkConnect(modelValue.value.hiveMetadataDbCfgs, type, (result, err) => {
            result2 = result
            err2 = err
            resultFunc(result2, callback, err2)
            timer2 = setTimeout(() => {
              timer2 = null
            }, 200)
          })
        }
      },
      trigger: 'blur'
    }
  }

  const formRules = {
    vNet: FormCheck.required("请选择网络", "change"),
    subNet: FormCheck.required("请选择子网", "change"),
    masterSecurityGroup: FormCheck.required("请选择主安全组", 'change'),
    slaveSecurityGroup: FormCheck.required("请选择子安全组", 'change'),
    "ambariDbCfgs.url": [FormCheck.required("请输入数据库主机"), ambariDbCfgsCheck(0)],
    "ambariDbCfgs.account": [FormCheck.required("请输入数据库用户名"), ambariDbCfgsCheck(0)],
    "ambariDbCfgs.password": [FormCheck.required("请输入数据库密码"), ambariDbCfgsCheck(0)],
    "ambariDbCfgs.port": [FormCheck.required("请输入数据库端口"), ambariDbCfgsCheck(0)],
    "ambariDbCfgs.database": [FormCheck.required("请输入数据库库名"), ambariDbCfgsCheck(0)],
    "hiveMetadataDbCfgs.url": [FormCheck.required("请输入数据库主机"), ambariDbCfgsCheck(1)],
    "hiveMetadataDbCfgs.account": [FormCheck.required("请输入数据库用户名"), ambariDbCfgsCheck(1)],
    "hiveMetadataDbCfgs.password": [FormCheck.required("请输入数据库密码"), ambariDbCfgsCheck(1)],
    "hiveMetadataDbCfgs.port": [FormCheck.required("请输入数据库端口"), ambariDbCfgsCheck(1)],
    "hiveMetadataDbCfgs.database": [FormCheck.required("请输入数据库库名"), ambariDbCfgsCheck(1)],
    keypairId: FormCheck.required("请选择登录方式", 'change'),
  }

  const hasHive = computed(() => {
    let clusterApps = modelValue.value.instanceGroupVersion.clusterApps

    let idx = clusterApps.findIndex(app => {
      return app.app_name == 'Hive'
    })

    return idx > -1
  })

  function nextEvent() {
    isNextClick.value = true
    stepLoading.value = true
    RefForm.value.validate((valid, fields) => {
      stepLoading.value = false
      if (valid) {
        emit('changeStep', 3)
      } else {
        ElMessage.error("部分填写数据出错，请检查填写数据！")
        console.log('error submit!', fields)
      }
      isNextClick.value = false
    })
  }
  function preEvent() {
    emit('changeStep', 1)
  }

  // 网络列表
  const netList = ref([])

  function getNetworkList() {
    clusterApi.getNetworkList().then(res => {
      if (res.result == true) {
        netList.value = res.data

        if (netList.value.length) {
          if (!modelValue.value.vNet) {
            modelValue.value.vNet = netList.value[0].dictValue
            vNetChange()
          }
        }
      } else {
        ElMessage.error("检查数据库连接失败！")
      }
    })
  }

  getNetworkList()

  function vNetChange() {
    let item = netList.value.find(itm => {
      return itm.dictValue == modelValue.value.vNet
    }) || {}

    modelValue.value.vNetName = item.dictName
  }

  // 子网络列表
  const subNetList = ref([])

  function getSubnetList() {
    clusterApi.getSubnetList().then(res => {
      if (res.result == true) {
        subNetList.value = res.data

        if (subNetList.value.length) {
          if (!modelValue.value.subNet) {
            modelValue.value.subNet = subNetList.value[0].resourceId
            subNetChange()
          }
        }
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getSubnetList()

  function subNetChange() {
    let item = subNetList.value.find(itm => {
      return itm.resourceId == modelValue.value.subNet
    }) || {}

    modelValue.value.subNetName = item.name
  }

  // 主安全组
  const masterSecurityGroupList = ref([])

  function getPrimarySecurityGroupList() {
    clusterApi.getPrimarySecurityGroupList().then(res => {
      if (res.result == true) {
        masterSecurityGroupList.value = res.data

        if (masterSecurityGroupList.value.length) {
          if (!modelValue.value.masterSecurityGroup) {
            modelValue.value.masterSecurityGroup = masterSecurityGroupList.value[0].dictValue
            masterSecurityGroupChange()
          }
        }
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getPrimarySecurityGroupList()

  function masterSecurityGroupChange() {
    let item = masterSecurityGroupList.value.find(itm => {
      return itm.dictValue == modelValue.value.masterSecurityGroup
    }) || {}

    modelValue.value.masterSecurityGroupName = item.dictName
  }

  // 子安全组
  const subSecurityGroupList = ref([])

  function getSubSecurityGroupList() {
    clusterApi.getSubSecurityGroupList().then(res => {
      if (res.result == true) {
        subSecurityGroupList.value = res.data

        if (subSecurityGroupList.value.length) {
          if (!modelValue.value.slaveSecurityGroup) {
            modelValue.value.slaveSecurityGroup = subSecurityGroupList.value[0].dictValue
            slaveSecurityGroupChange()
          }
        }
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getSubSecurityGroupList()

  function slaveSecurityGroupChange() {
    let item = subSecurityGroupList.value.find(itm => {
      return itm.dictValue == modelValue.value.slaveSecurityGroup
    }) || {}

    modelValue.value.slaveSecurityGroupName = item.dictName
  }

  // 密钥对
  const keypairList = ref([])

  function getKeypairList() {
    clusterApi.getKeypairList().then(res => {
      if (res.result == true) {
        keypairList.value = res.data
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getKeypairList()

  // 系统盘类型
  const osDiskTypeList = ref([])

  function getOsDiskTypeList() {
    clusterApi.getOsDiskTypeList().then(res => {
      if (res.result == true) {
        osDiskTypeList.value = res.data
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getOsDiskTypeList()

  // 节点信息
  const vmskuList = ref([])

  function getVmskuList() {
    clusterApi.getVmskuList().then(res => {
      if (res.result == true) {
        let arr = res.data || []
        arr.forEach(item => {
          item.name = item.skuName + ' ' + item.vCPUs + '核' + item.memoryGB + 'G'
          item.sname = item.vCPUs + '核' + item.memoryGB + 'G'
        })
        vmskuList.value = arr

        if (arr.length) {
          let firstObj = arr[0]

          modelValue.value.instanceGroupSkuCfgs.forEach(item => {
            if (!item.skuName) {
              item.skuName = firstObj.skuName
              item.vCPUs = firstObj.vCPUs
              item.memoryGB = firstObj.memoryGB
            }
          })
        }
      } else {
        ElMessage.error(res.errorMsg)
      }
    })
  }

  getVmskuList()

  const refresh = ref(true)

  function osDiskChange() {
    modelValue.value.instanceGroupSkuCfgs.forEach(item => {
      item.osVolumeType = modelValue.value.osDiskType
      item.osVolumeSize = modelValue.value.diskSize
    })

    refresh.value = false
    nextTick(() => {
      refresh.value = true
    })
  }

  function diskTypeToName(type) {
    let item = osDiskTypeList.value.find(itm => {
      return itm.dictValue == type
    }) || {}

    return item.dictName
  }

  let dialogVisible = ref(false)

  const editForm = reactive({
    vmskuName: "",
    dataVolumeType: "",
    dataVolumeSize: "",
    editItem: {}
  })



  const scriptFormRules = {
    vmskuName: FormCheck.required("请选择规格"),
    disk: {
      validator: (rule, value, callback) => {

        let reg = /^[1-9][0-9]*$/;
        if (!reg.test(editForm.dataVolumeSize)) {
          return callback(new Error("数据盘大小仅支持正整数"));
        }

        if (editForm.dataVolumeSize < 200) {
          return callback(new Error("数据盘不能小于200G！"));
        } else if (editForm.dataVolumeSize > 100000) {
          return callback(new Error("数据盘不能大于100000G！"));
        }
        return callback();
      },
      trigger: 'blur'
    }
  }

  const Ref_editForm = ref(null)

  function editDisk(item) {
    console.log(item)
    editForm.vmskuName = item.skuName + ' ' + item.vCPUs + '核' + item.memoryGB + 'G'
    editForm.dataVolumeType = item.dataVolumeType
    editForm.dataVolumeSize = item.dataVolumeSize || 0

    editForm.editItem = item

    dialogVisible.value = true
  }
  
  function saveEvent() {
    Ref_editForm.value.validate((valid, fields) => {
      if (valid) {
        console.log(editForm.vmskuName)
        let item = vmskuList.value.find(itm => {
          return itm.name == editForm.vmskuName
        }) || {}

        editForm.editItem.vCPUs = item.vCPUs
        editForm.editItem.memoryGB = item.memoryGB
        editForm.editItem.skuName = item.skuName

        editForm.editItem.dataVolumeType = editForm.dataVolumeType
        editForm.editItem.dataVolumeSize = editForm.dataVolumeSize

        if (editForm.editItem.vmRole == 'Ambari') {

          let instanceGroupSkuCfgs = modelValue.value.instanceGroupSkuCfgs || []
          let itemMaster = instanceGroupSkuCfgs.find(item => {
            return item.vmRole == "Master"
          })

          itemMaster.vCPUs = item.vCPUs
          itemMaster.memoryGB = item.memoryGB
          itemMaster.skuName = item.skuName

          itemMaster.dataVolumeType = editForm.dataVolumeType
          itemMaster.dataVolumeSize = editForm.dataVolumeSize
        }

        dialogVisible.value = false
      } else {
        console.log('error submit!', fields)
      }
    })
  }

  function isHaChange() {
    if (modelValue.value.isHa) {
      let instanceGroupSkuCfgs = modelValue.value.instanceGroupSkuCfgs || []
      let itemMaster = instanceGroupSkuCfgs.find(item => {
        return item.vmRole == "Master"
      })
      itemMaster.cnt = 2

      let itemAmbari = instanceGroupSkuCfgs.find(item => {
        return item.vmRole == "Ambari"
      })
      itemAmbari.cnt = 1
    } else {
      let instanceGroupSkuCfgs = modelValue.value.instanceGroupSkuCfgs || []
      let itemMaster = instanceGroupSkuCfgs.find(item => {
        return item.vmRole == "Master"
      })
      itemMaster.cnt = 0
    }
  }
  
  function objectSpanMethod({row, column, rowIndex, columnIndex,}) {
    if (modelValue.value.isHa == 1) {
      if (columnIndex === 1) {
        if (rowIndex === 0) {
          return {
            rowspan: 2,
            colspan: 1,
          }
        } else if (rowIndex === 1) {
          return {
            rowspan: 0,
            colspan: 0,
          }
        }
      }
    }
    return {
      rowspan: 1,
      colspan: 1,
    }
  }

</script>

